// import apiKey from '@/env'
import api from '@/api'


const authModule = {
    namespaced: true,
    state() {
        return {
            error: '',
            loading: false,
            userInfo: {
                token: '',
                email: '',
                userId: '',
                refreshToken: '',
            }
        }
    },
    mutations: {
        login(state, user) {
            state.isLoggedIn = true;
            state.user = user
        },
        logout(state) {
            state.isLoggedIn = false,
            state.user = null
        }
    },
    actions: {

        async auth({ state, commit }, payLoad) {
            state.error = ''
            state.loading = true


            console.log('commit:', commit)
            try {

                let response = await api.post('/api/login', {
                    email: payLoad.email,
                    password: payLoad.password,
                    returnSecureToken: true
                })


                // let response = await api.post('/api/login', payLoad, {
                //     headers: {
                //       'Content-Type': 'application/json'
                //     }
                //   });
                
                // const response = await fetch('/api/login', {
                //     method: 'POST',
                //     headers: {
                //       'Content-Type': 'application/json'
                //     },
                //     body: JSON.stringify(payLoad)
                //   });


                // let response1 = await api.post(`https://identitytoolkit.googleapis.com/v1/accounts:${payLoad.type}?key=${apiKey}`, {
                //     email: payLoad.email,
                //     password: payLoad.password,
                //     returnSecureToken: true
                // })

                console.log(response)
                console.log(response.data)


                state.userInfo = {
                    token: response.data.token,
                    email: response.data.email,
                    userId: response.data.userId,
                    refreshToken: response.data.refreshToken,
                    expiresIn: response.data.expiresIn
                }


                localStorage.setItem('userTokens', 
                    JSON.stringify({
                        token: state.userInfo.token, 
                        refreshToken: state.userInfo.refreshToken,
                    }))

            } catch (e) {
                state.error = e.response.data.error.message  
                throw state.error

            } finally {
                state.loading = false
            }

        },
        logout({state}) {
            state.userInfo = {
                token: '',
                email: '',
                userId: '',
                refreshToken: '',
            }
        }
    }
}

export default authModule



